import * as amplitude from '@amplitude/analytics-browser';
import { IS_PRODUCTION } from '../Constants/constant';

const AMPLITUDE_API_KEY = '9fbfb678c4acf2377b4aaa7b95116be0';

amplitude.init(AMPLITUDE_API_KEY, {
  autocapture: false,
});


export const trackEvent = (eventName, eventProperties = {}) => {
  const userId = localStorage.getItem('CQ_USER_NUMBER');
  if (userId) {
    amplitude.setUserId(userId);
  }
  if (!IS_PRODUCTION) {
    console.log('Tracking Event:', eventName, eventProperties);
    return;
  }
  amplitude.track(eventName, eventProperties);
}