import { captureException } from "./utils/sentry";
import "@radix-ui/themes/styles.css";
import { injectScript } from './utils/InjectScript';
import { sendMessage, postWindowMessage } from './utils/messageSender';
import { trackEvent } from "./utils/trackEvent";
import { saveSummaryToStorage } from './utils/renderChatSummaryPanel';


setTimeout(() => {
  injectScript('static/js/wpp.js');
}, 2000);

async function getAuthPayload(payload = {}) {
  const userNumber = localStorage.getItem('CQ_USER_NUMBER');
  const token = localStorage.getItem('USER_AUTH_TOKEN');
  const userId = localStorage.getItem('USER_AUTH_ID');

  if (userId !== null && userId !== undefined) {
    payload.loggedUserId = userId;
  }
  if (userNumber !== null && userNumber !== undefined) {
    payload.loggedUserNumber = userNumber;
  }
  if (token !== null && token !== undefined) {
    payload.loggedToken = token;
  }
  return payload;
}

async function postIconUrl() {
  const iconUrl = chrome.runtime.getURL('static/images/the_chat_quotient_logo_48.png');
  postWindowMessage('CQ_ICON_URL', { iconUrl: iconUrl });
}

// Wait a moment to ensure WPP is set up
setTimeout(() => {
  injectScript('static/js/injector.js');
}, 5000);

async function GenerateToken() {
  const payload = {};
  const userId = localStorage.getItem('USER_AUTH_ID');
  if (userId) {
    payload.userId = userId;
  }
  const tokenResponse = await sendMessage('FETCH_USER_TOKEN', payload);
  if (tokenResponse.success) {
    localStorage.setItem('IS_AUTHENTICATED', 'true');
    localStorage.setItem('IS_USER_ONBOARDED', 'true');
    localStorage.setItem('USER_AUTH_TOKEN', tokenResponse.data.token);
    postIconUrl();

  } else {
    localStorage.removeItem('IS_AUTHENTICATED');
    postWindowMessage('USER_LOGIN_FAIL', { loginStatus: false });
  }
}

async function fetchStaticMessages() {
  const response = await sendMessage('FETCH_STATIC_MESSAGES');
  if (response.success) {
    localStorage.setItem('CQ_STATIC_MESSAGES', JSON.stringify(response.data));
  } else {
    captureException('Failed to fetch static messages:', response.error);
  }
}

async function fetchSystemConfig() {
  localStorage.removeItem('CQ_SYSTEM_CONFIG');
  const response = await sendMessage('FETCH_SYSTEM_CONFIG');
  if (response.success) {
    localStorage.setItem('CQ_SYSTEM_CONFIG', JSON.stringify(response.data));
  } else {
    captureException('Failed to fetch system config:', response.error);
  }
}

async function UserLogin(payload) {
  await fetchSystemConfig();
  await fetchStaticMessages();
  if (localStorage.getItem('USER_AUTH_ID')) {
    await GenerateToken(localStorage.getItem('USER_AUTH_ID'));
    return;
  }
  const loginResponse = await sendMessage('CALL_USER_LOGIN', payload);
  if (loginResponse.success) {
    localStorage.setItem('USER_AUTH_ID', loginResponse.data.user_id);
    localStorage.setItem('CQ_USER_TRIAL_EXPIRY', loginResponse.data.trial_expiry);
    await GenerateToken();
  } else {
    localStorage.removeItem('IS_AUTHENTICATED');
    postWindowMessage('USER_LOGIN_FAIL', { loginStatus: false });
  }
}

function transformUserConfig(input) {
  if (!input || !input.user || !input.config) return {};
  localStorage.setItem('CQ_USER_TRIAL_EXPIRY', input.user.trial_expiry);

  return {
    name: input.user.name || '',
    number: input.user.number || '',
    industry: input.config.industry || '',
    profession: input.config.profession || [],
    tone: (input.config.tone || []).filter(Boolean), // remove falsy if any
    custom_instruction: input.config.custom_instruction || '',
    // createdAt: input.user.created_at || '',
    trialExpiry: input.user.trial_expiry || '',
  };
}

// Track processed requests to prevent duplicates
const processedRequests = new Set();

window.addEventListener('message', async (event) => {
  if (event.source !== window) return;
  const payload = await getAuthPayload(event.data.payload || {}) || {};
  const type = event.data.type;

  if (type === 'USER_LOGIN') {
    if (localStorage.getItem('IS_AUTHENTICATED') === 'true') {
      return;
    }
    await UserLogin(payload);

  }
  if (type === 'ONBOARDING_FORM_SUBMIT') {
    // payload.token = localStorage.getItem('USER_AUTH_TOKEN');
    const response = await sendMessage('ONBOARDING_FORM_SUBMIT', payload);
    if (response.success) {
      localStorage.setItem('USER_AUTH_ID', response.data.user_id)
      localStorage.setItem('USER_AUTH_TOKEN', response.data.token);
      localStorage.setItem('IS_USER_ONBOARDED', 'true');
      localStorage.setItem('IS_AUTHENTICATED', 'true');
      postIconUrl();
    } else {
      captureException('Failed to submit onboarding form:', response.error);
    }
  }
  if (['GET_AI_REPLY', 'GET_REPHRASE_REPLY', 'GET_ADVANCE_GENERATE_REPLY', 'GET_ADVANCE_REPHRASE_REPLY', 'GET_AI_RELOAD_REPLY'].includes(type)) {
    const response = await sendMessage('GET_AI_REPLY', payload);
    response.type = type;
    if (response.success) {
      postWindowMessage('AI_REPLY', response);
    } else {
      captureException('Failed to get AI reply:', response.error);
      postWindowMessage('AI_REPLY_ERROR', response);
    }
  }

  if (type === 'POST_REPLY_REQUEST_ID') {
    await sendMessage('POST_REPLY_REQUEST_ID', payload);
  }

  if (type === 'GET_USER_CONFIG') {
    const response = await sendMessage('GET_USER_CONFIG', payload);
    if (response.success) {
      postWindowMessage('USER_CONFIG', transformUserConfig(response.data));
    } else {
      captureException('Failed to get User config:', response.error);
      postWindowMessage('USER_CONFIG_ERROR', response);
    }
  }
  if (type === 'UPDATE_USER_CONFIG') {
    const response = await sendMessage('UPDATE_USER_CONFIG', payload);
    if (response.success) {
      postWindowMessage('USER_CONFIG_UPDATE', response.data);
    } else {
      captureException('Failed to update User config:', response.error);
      postWindowMessage('USER_CONFIG_UPDATE_ERROR', response);
    }
  }
  if (type === 'INJECTOR_ERROR' || type === 'INJECTOR_UNHANDLED_REJECTION') {
    captureException(new Error(payload.error || 'Injector error'));
  }

  if (type == 'TRACK_EVENT') {
    const eventName = payload.eventName;
    const eventProperties = payload.properties || {};
    if (eventName) {
      trackEvent(eventName, eventProperties);
    } else {
      captureException('Track event called without event name');
    }
  }

  if (type == 'GET_CHAT_SUMMARY') {
    const response = await sendMessage('GET_CHAT_SUMMARY', payload);
    if (response.success) {
      postWindowMessage('CHAT_SUMMARY', response.data);
      saveSummaryToStorage(response.data.summary, payload.originalChatId, payload.lastMessageTimestamp);
    } else {
      captureException('Failed to get chat summary:', response.error);
      postWindowMessage('CHAT_SUMMARY_ERROR', response);
    }
  }

  if (type == 'SUBMIT_FEEDBACK') {
    const response = await sendMessage('SUBMIT_FEEDBACK', payload);
    if (response.success) {
      postWindowMessage('FEEDBACK_SUCCESS', response.data);
    } else {
      captureException('Failed to submit feedback:', response.error);
      postWindowMessage('FEEDBACK_ERROR', response);
    }
  }

  if (type === 'FETCH_QUICK_REPLIES') {
    const response = await sendMessage('FETCH_QUICK_REPLIES', payload);
    if (response.success) {
      postWindowMessage('QUICK_REPLIES_LOADED', response.data);
    } else {
      captureException('Failed to fetch quick replies:', response.error);
      postWindowMessage('QUICK_REPLIES_ERROR', response);
    }
  }

  if (type === 'SAVE_QUICK_REPLIES') {
    const response = await sendMessage('SAVE_QUICK_REPLIES', payload);
    if (response.success) {
      postWindowMessage('QUICK_REPLIES_SAVED', response.data);
    } else {
      captureException('Failed to save quick replies:', response.error);
      postWindowMessage('QUICK_REPLIES_SAVE_ERROR', response);
    }
  }

  if (type === 'SCHEDULED_MESSAGE_DELIVERED') {
    await sendMessage('SCHEDULED_MESSAGE_DELIVERED', payload);
    console.log('Notified background of scheduled message delivery');
  }

  if (type === 'SCHEDULED_MESSAGE_FAILED') {
    await sendMessage('SCHEDULED_MESSAGE_FAILED', payload);
    console.log('Notified background of scheduled message delivery');
  }

  if (['CHAT_ARTIFACTS_FETCH', 'CHAT_ARTIFACTS_CREATE', 'CHAT_ARTIFACTS_UPDATE', 'CHAT_ARTIFACTS_DELETE'].includes(type)) {
    await sendMessage(type, payload);
  }

  if (type === 'CHAT_DATA_COUNTS_INIT') {
    await sendMessage('CHAT_DATA_COUNTS_INIT', payload);
  }

  if (type === 'CHAT_DATA_COUNTS_GET') {
    const response = await sendMessage('CHAT_DATA_COUNTS_GET', payload);
    if (response?.success) {
      postWindowMessage('CHAT_DATA_COUNTS', { chatId: payload.chatId, counts: response.data });
    } else {
      postWindowMessage('CHAT_DATA_COUNTS_ERROR', { chatId: payload.chatId, error: response?.error || 'Failed to fetch counts' });
    }
  }

  if (type === 'DELETE_INDEXED_DB') {
    const response = await sendMessage('DELETE_INDEXED_DB', payload);
    postWindowMessage('INDEXED_DB_DELETED', { success: !!response?.success });
  }

  // Only forward customer requests FROM component TO background (not responses)
  // Responses have actionType, requests don't
  if (['CUSTOMER_CREATE', 'CUSTOMER_UPDATE', 'CUSTOMER_GET', 'CUSTOMER_DELETE', 'COMPANIES_GET', 'COMPANY_CREATE', 'COMPANY_UPDATE'].includes(type) && !payload.actionType) {
    const resp = await sendMessage(type, payload);
    // Forward company errors to window so UI can show them
    if (type === 'COMPANY_CREATE' || type === 'COMPANY_UPDATE') {
      let errorMsg = resp.data.error || 'Failed to save company';
      // Normalize generic error strings to only the API's message after 'error '
      if (typeof errorMsg === 'string') {
        const idx = errorMsg.toLowerCase().indexOf('error ');
        if (idx !== -1) {
          const trimmed = errorMsg.slice(idx + 6).trim();
          if (trimmed) errorMsg = trimmed;
        }
      }
      postWindowMessage('COMPANIES_UPDATE', {
        actionType: 'COMPANY_ERROR',
        data: { error: errorMsg }
      });
    }
  }
});

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
  if (message.type === 'GENERATE_TOKEN') {
    localStorage.setItem('USER_AUTH_TOKEN', message.token);
    sendResponse({ success: true });
    return true;
  }

  if (message.action === 'GET_USER_NUMBER') {
    const userNumber = localStorage.getItem('CQ_USER_NUMBER');
    sendResponse({ userNumber });
    return true;
  }

  if (message.type === 'DELIVER_SCHEDULED_MESSAGE') {
    // Forward scheduled message delivery to injector
    postWindowMessage('DELIVER_SCHEDULED_MESSAGE', message.payload);
    sendResponse({ success: true });
    return true;
  }

  if(message.type == 'CHAT_ARTIFACT_UPDATE'){
    postWindowMessage('CHAT_ARTIFACT_UPDATE', message);
    sendResponse({ success: true });
    return true;
  }

  if(message.type == 'CUSTOMER_UPDATE'){
    postWindowMessage('CUSTOMER_UPDATE', {
      actionType: message.actionType,
      data: message.data
    });
    sendResponse({ success: true });
    return true;
  }

  if(message.type == 'COMPANIES_UPDATE'){
    postWindowMessage('COMPANIES_UPDATE', {
      actionType: message.actionType,
      data: message.data
    });
    sendResponse({ success: true });
    return true;
  }

  if (message.type === 'TRACK_EVENT_FROM_BACKGROUND') {
    const eventName = message.payload?.eventName;
    const eventProperties = message.payload?.properties || {};
    if (eventName) {
      trackEvent(eventName, eventProperties);
      sendResponse({ success: true });
    } else {
      captureException('Track event from background called without event name');
      sendResponse({ success: true });
    }
    return true;
  }
});
